import { defineStore } from 'pinia'
import _ from 'lodash'
import helpers from '@/helpers'
import axios from '@/http'
import { useMainStore } from '@/stores/main'
import notifier from '../notifier'

let idCounter = 0

function newTab () {
  return _.cloneDeep({
    id: ++idCounter,
    name: 'Tab 0',
    form: helpers.getDefaultFromLocalStorage('optunaForm', {
      start_date: '2021-01-01',
      finish_date: '2021-06-01',
      debug_mode: false,
      routes: [],
      extra_routes: [],
      optimal_total: 50,
      optimizer: 'NSGAIISampler',
      fitnessMetric1: 'Sharpe',
      fitnessMetric2: 'MaxDrawdown',
      isMultivariate: false,
      secondObjectiveDirection: 'Minimize',
      consider_prior: true,
      prior_weight: 1.0,
      n_startup_trials: 10,
      n_ei_candidates: 24,
      group: false,
      sigma: 0.5,
      consider_pruned_trials: false,
      population_size: 500,
      crossover_prob: 0.9,
      swapping_prob: 0.5,
      qmc_type: 'sobol',
      scramble: true,
      n_trials: 1000,
      do_analysis: false,
      validation_interval: 90,
      robust_test_iteration_count: 3,
      robust_test_max: 6.0,
      robust_test_min: 1.5,
      max_final_number_of_validation_results: 64,
      optuna_visualizations: false
    }),
    results: {
      showResults: false,
      executing: false,
      logsModal: false,
      conductingAnalysis: false,
      progressString: '',
      analysisFinished: false,
      progressbar: {
        current: 0,
        estimated_remaining_seconds: 0
      },
      routes_info: [],
      best_candidates: [],
      duration_array: [],
      metrics: [],
      generalInfo: {},
      infoLogs: '',
      exception: {
        error: '',
        traceback: ''
      },
      alert: {
        message: '',
        type: ''
      }
    }
  })
}

export const useOptunaStore = defineStore({
  id: 'optuna',
  state: () => ({
    tabs: {
      1: newTab()
    }
  }),
  data () {
    return {
      isConfirmPromptActive: false,
      // ... other data properties
    }
  },
  actions: {
    start (id, continueExistingStudy = false, checked_study = false, doAnalysis = false) {
      if (doAnalysis === false) {
        this.tabs[id].results.progressbar.current = 0
        this.tabs[id].results.executing = true
        this.tabs[id].results.infoLogs = ''
        this.tabs[id].results.exception.traceback = ''
        this.tabs[id].results.exception.error = ''
        this.tabs[id].results.alert.message = ''
        this.tabs[id].results.alert.type = ''
        this.tabs[id].results.metrics = []
        this.tabs[id].results.generalInfo = {}
        this.tabs[id].results.best_candidates = []
        this.tabs[id].results.duration_array = []
        this.tabs[id].results.routes_info = []
        this.tabs[id].results.showResults = false

        // make sure symbols are uppercase
        this.tabs[id].form.routes = this.tabs[id].form.routes.map(route => {
          route.symbol = route.symbol.toUpperCase()
          return route
        })
        // also for extra_routes
        this.tabs[id].form.extra_routes = this.tabs[id].form.extra_routes.map(route => {
          route.symbol = route.symbol.toUpperCase()
          return route
        })
      }
      this.tabs[id].results.conductingAnalysis = doAnalysis
      this.tabs[id].results.analysisFinished = false
      this.tabs[id].results.progressString = ''
      const mainStore = useMainStore()

      const params = {
        id,
        checked_study: checked_study,
        continueExistingStudy: continueExistingStudy,
        routes: this.tabs[id].form.routes,
        extra_routes: this.tabs[id].form.extra_routes,
        config: mainStore.settings.optuna,
        start_date: this.tabs[id].form.start_date,
        finish_date: this.tabs[id].form.finish_date,
        optimal_total: this.tabs[id].form.optimal_total,
        debug_mode: this.tabs[id].form.debug_mode,
        optimizer: this.tabs[id].form.optimizer,
        fitnessMetric1: this.tabs[id].form.fitnessMetric1,
        fitnessMetric2: this.tabs[id].form.fitnessMetric2,
        isMultivariate: this.tabs[id].form.isMultivariate,
        secondObjectiveDirection: this.tabs[id].form.secondObjectiveDirection,
        consider_prior: this.tabs[id].form.consider_prior,
        prior_weight: this.tabs[id].form.prior_weight,
        n_startup_trials: this.tabs[id].form.n_startup_trials,
        n_ei_candidates: this.tabs[id].form.n_ei_candidates,
        group: this.tabs[id].form.group,
        sigma: this.tabs[id].form.sigma,
        consider_pruned_trials: this.tabs[id].form.consider_pruned_trials,
        population_size: this.tabs[id].form.population_size,
        crossover_prob: this.tabs[id].form.crossover_prob,
        swapping_prob: this.tabs[id].form.swapping_prob,
        qmc_type: this.tabs[id].form.qmc_type,
        scramble: this.tabs[id].form.scramble,
        n_trials: this.tabs[id].form.n_trials,
        do_analysis: doAnalysis,
        validation_interval: this.tabs[id].form.validation_interval,
        robust_test_iteration_count: this.tabs[id].form.robust_test_iteration_count,
        robust_test_max: this.tabs[id].form.robust_test_max,
        robust_test_min: this.tabs[id].form.robust_test_min,
        max_final_number_of_validation_results: this.tabs[id].form.max_final_number_of_validation_results,
        optuna_visualizations: this.tabs[id].form.optuna_visualizations
      }
      if (doAnalysis === true) {
        axios.post('/optuna', params)
          .catch(error => {
            notifier.error(`[${error.response.status}]: ${error.response.statusText}`)
            this.tabs[id].results.executing = false
          })
      }
      this.submitOptunaRequest(id, params)
    },
    submitOptunaRequest (id, params) {
      if (this.isConfirmPromptActive) {
        return
      }
      axios.post('/optuna', params)
        .then(response => {
          if (response.data.study_exists && !params.checked_study) {
            this.isConfirmPromptActive = true
            const userWantsToContinue = confirm(response.data.message + ' Do you want to resume optimization?')
            this.isConfirmPromptActive = false

            params = { ...params }
            if (userWantsToContinue) {
              params.continueExistingStudy = true
            } else {
              params.continueExistingStudy = false
            }
            params.checked_study = true
            this.submitOptunaRequest(id, params)
          }
        })
        .catch(error => {
        // Handle errors
          notifier.error(`[${error.response.status}]: ${error.response.statusText}`)
          this.tabs[id].results.executing = false
        })
    },
    cancel (id) {
      if (this.tabs[id].results.exception.error) {
        this.tabs[id].results.executing = false
        return
      }
      axios.delete('/optuna', {
        headers: {},
        data: {
          id
        }
      }).catch(error => notifier.error(`[${error.response.status}]: ${error.response.statusText}`))
    },
    rerun (id) {
      this.tabs[id].results.showResults = false
      this.start(id)
    },
    newOptuna (id) {
      this.tabs[id].results.showResults = false
    },

    candlesInfoEvent (id, data) {
      this.tabs[id].results.info = [
        ['Period', data.duration],
        [
          'Starting-Ending Date',
          `${helpers.timestampToDate(
            data.starting_time
          )} => ${helpers.timestampToDate(data.finishing_time)}`
        ]
      ]
    },
    routesInfoEvent (id, data) {
      const arr = [['Exchange', 'Symbol', 'Timeframe', 'Strategy']]
      data.forEach(item => {
        arr.push([
          { value: item.exchange, style: '' },
          { value: item.symbol, style: '' },
          { value: item.timeframe, style: '' },
          { value: item.strategy_name, style: '' },
        ])
      })
      this.tabs[id].results.routes_info = arr
    },
    progressbarEvent (id, data) {
      this.tabs[id].results.progressbar = data
    },
    infoLogEvent (id, data) {
      this.tabs[id].results.infoLogs += `[${helpers.timestampToTime(
        data.timestamp
      )}] ${data.message}\n`
    },
    exceptionEvent (id, data) {
      this.tabs[id].results.exception.error = data.error
      this.tabs[id].results.exception.traceback = data.traceback
    },
    generalInfoEvent (id, data) {
      if (!this.tabs[id].results.executing) {
        this.tabs[id].results.executing = true
      }

      // Check if data.average_execution_seconds has a value
      if (data.average_execution_seconds != null) {
        this.tabs[id].results.duration_array.push(data.average_execution_seconds)
      } else {
        // If duration_array is not empty, push the last value onto itself
        if (this.tabs[id].results.duration_array.length > 0) {
          const lastValue = this.tabs[id].results.duration_array[this.tabs[id].results.duration_array.length - 1]
          this.tabs[id].results.duration_array.push(lastValue)
        }
      }

      // Calculate mean
      const mean = this.tabs[id].results.duration_array.reduce((sum, value) => sum + value, 0) / this.tabs[id].results.duration_array.length

      this.tabs[id].results.generalInfo = [
        ['Started at', data.started_at],
        ['Index', data.index],
        ['Average strategy execution time', `${_.round(mean, 2)} seconds`],
        ['Trading route', data.trading_route],
        ['Optimizer', data.optimizer],
      ]
    },
    metricsEvent (id, data) {
      // no trades were executed
      if (data === null) {
        this.tabs[id].results.metrics = []
        return
      }

      this.tabs[id].results.metrics = [
        ['Total Closed Trades', data.total],
        ['Total Net Profit', `${_.round(data.net_profit, 2)} (${_.round(data.net_profit_percentage, 2)}%)`],
        ['Starting => Finishing Balance', `${_.round(data.starting_balance, 2)} => ${_.round(data.finishing_balance, 2)}`],
        ['Open Trades', data.total_open_trades],
        ['Total Paid Fees', _.round(data.fee, 2)],
        ['Max Drawdown', _.round(data.max_drawdown, 2)],
        ['Annual Return', `${_.round(data.annual_return, 2)}%`],
        ['Expectancy', `${_.round(data.expectancy, 2)} (${_.round(data.expectancy_percentage, 2)}%)`],
        ['Avg Win | Avg Loss', `${_.round(data.average_win, 2)} | ${_.round(data.average_loss, 2)}`],
        ['Ratio Avg Win / Avg Loss', _.round(data.ratio_avg_win_loss, 2)],
        ['Win-rate', `${_.round(data.win_rate * 100, 2)}%`],
        ['Longs | Shorts', `${_.round(data.longs_percentage, 2)}% | ${_.round(data.shorts_percentage, 2)}%`],
        ['Avg Holding Time', data.average_holding_period],
        ['Winning Trades Avg Holding Time', data.average_winning_holding_period],
        ['Losing Trades Avg Holding Time', data.average_losing_holding_period],
        ['Sharpe Ratio', _.round(data.sharpe_ratio, 2)],
        ['Calmar Ratio', _.round(data.calmar_ratio, 2)],
        ['Sortino Ratio', _.round(data.sortino_ratio, 2)],
        ['Omega Ratio', _.round(data.omega_ratio, 2)],
        ['Winning Streak', data.winning_streak],
        ['Losing Streak', data.losing_streak],
        ['Largest Winning Trade', _.round(data.largest_winning_trade, 2)],
        ['Largest Losing Trade', _.round(data.largest_losing_trade, 2)],
        ['Total Winning Trades', data.total_winning_trades],
        ['Total Losing Trades', data.total_losing_trades]
      ]
    },
    terminationEvent (id) {
      if (this.tabs[id].results.executing) {
        this.tabs[id].results.executing = false
        notifier.success('Session terminated successfully')
      }
    },
    bestCandidatesEvent (id, items) {
      if (!Array.isArray(items)) {
        items = [items]
      }
      if (this.tabs[id].results.best_candidates.length === 0) {
        this.tabs[id].results.best_candidates = [
          ['Rank', 'DNA', items[0]?.fitness_description, 'Training-Testing Win-rate', 'Training-Testing total trades', 'Training-Testing PNL']
        ]
      }
      items.forEach(item => {
        if (!this.tabs[id].results.best_candidates.some(candidate => JSON.stringify(candidate[1].hyperparameters) === JSON.stringify(item.hyperparameters))) {
          const newItem = [
            { value: '', style: '' },
            { value: item.dna, style: '', tag: 'code', modalTrigger: true, hyperparameters: item.hyperparameters },
            { value: item.fitness, style: '' },
            { value: `${item.training_win_rate}% | ${item.testing_win_rate}%`, style: '' },
            { value: `${item.training_total_trades} | ${item.testing_total_trades}`, style: '' },
            { value: `${item.training_pnl}% | ${item.testing_pnl}%`, style: '' },
          ]
          this.tabs[id].results.best_candidates.push(newItem)
        }
      })
      const sortedRows = this.tabs[id].results.best_candidates.slice(1)
        .sort((a, b) => {
          const aFitness = Array.isArray(a[2].value) ? a[2].value[0] : a[2].value
          const bFitness = Array.isArray(b[2].value) ? b[2].value[0] : b[2].value
          return bFitness - aFitness
        }).slice(0, 100)
      sortedRows.forEach((item, index) => {
        item[0].value = `#${index + 1}` // Rank starts from 1
      })
      this.tabs[id].results.best_candidates = [
        this.tabs[id].results.best_candidates[0],
        ...sortedRows
      ]
    },
    alertEvent (id, data) {
      this.tabs[id].results.alert = data
      this.tabs[id].results.executing = false
      this.tabs[id].results.showResults = true
    },
    conductingAnalysisEvent (id) {
      this.tabs[id].results.conductingAnalysis = true
    },
    progressStringEvent (id, string) {
      this.tabs[id].results.progressString = string
    },
    analysisFinishedEvent (id) {
      this.tabs[id].results.analysisFinished = true
    },
  }
})
