<template>
  <LayoutWithSidebar>
    <template #left>
      <!-- alert -->
      <div v-if="results.alert.message">
        <Alert :data="results.alert"/>
      </div>

      <!-- exception  -->
      <div v-if="results.exception.error && results.executing" class="mb-8">
        <Exception :title="results.exception.error"
                   :content="results.exception.traceback"
                   mode="optuna"
        />
      </div>

      <!-- Execution -->
      <div v-if="(results.executing || results.showResults) && !results.exception.error">
        <Divider title="Info"/>
        <KeyValueTable v-if="results.generalInfo.length" :data="results.generalInfo" />
        <TablePlaceholder v-else/>

        <div v-if="results.best_candidates.length" class="mt-16">
          <Divider title="Best Candidates"/>
          <MultipleValuesTable :data="results.best_candidates" header/>
        </div>
        <div v-if="!results.executing && results.showResults && results.best_candidates.length === 0" class="text-center">
          <h2 class="text-red-500 font-bold mt-8">No optimization trials were successful. Please try again.</h2>
        </div>
      </div>

      <!-- Content -->
      <div v-if="!results.executing && !results.showResults">
        <Routes v-if="isInitiated" :form="form" :results="results"/>
        <div class="flex items-center">
          <!-- Optuna Sampler Selection -->
          <select v-model="form.optimizer" v-tooltip="selectedSamplerDescription" :class="selectClass">
            <option disabled value="">Please select a sampler</option>
            <option value="GridSampler">Grid Sampler</option>
            <option value="RandomSampler">Random Sampler</option>
            <option value="TPESampler">TPESampler</option>
            <option value="CmaEsSampler">CmaEsSampler</option>
            <option value="NSGAIISampler">NSGAIISampler</option>
            <option value="NSGAIIISampler">NSGAIIISampler</option>
            <option value="MOTPESampler">MOTPESampler</option>
            <option value="QMCSampler">QMCSampler</option>
            <option value="BruteForceSampler">BruteForceSampler</option>
          </select>
        </div>
        <div class="mt-4">
          <label for="n_trials">Number of Optuna Trials:</label>
          <input id="n_trials" v-model.number="form.n_trials" v-tooltip="'Number of trials to run. Each trial is a set of hyperparameters to test.'" type="number"
                 min="1" :class="inputClass" class="border-gray-300 rounded-md shadow-sm">
        </div>
        <Divider class="mt-16" title="Optimizer Settings"/>
        <!-- Optimizer-specific settings -->
        <!-- These can be conditionally rendered based on the selected optimizer -->
        <div v-if="form.optimizer === 'TPESampler'">
          <!-- Inputs for settings specific to TPESampler -->
          <div>
            <label for="consider_prior">Consider Prior:</label>
            <input id="consider_prior" v-model="form.consider_prior" v-tooltip="'Includes the prior in Gaussian Process. True makes the algorithm exploitation-oriented.'" :class="inputClass"
                   type="checkbox">
          </div>
          <div>
            <label for="prior_weight">Prior Weight:</label>
            <input id="prior_weight" v-model="form.prior_weight" v-tooltip="'The weight of the prior. Higher values prioritize past results.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="n_startup_trials">Number of Startup Trials:</label>
            <input id="n_startup_trials" v-model="form.n_startup_trials" v-tooltip="'Initial number of trials for random sampling before optimization.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="n_ei_candidates">Number of EI Candidates:</label>
            <input id="n_ei_candidates" v-model="form.n_ei_candidates" v-tooltip="'Number of candidate samples for calculating Expected Improvement.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="group">Group:</label>
            <input id="group" v-model="form.group" v-tooltip="'Groups related parameters for joint sampling.'" :class="inputClass"
                   type="checkbox">
          </div>
        </div>
        <div v-if="form.optimizer === 'NSGAIISampler'">
          <!-- Inputs for settings specific to NSGAIISampler -->
          <div>
            <label for="population_size">Population Size:</label>
            <input id="population_size" v-model="form.population_size" v-tooltip="'Number of individuals in each generation for genetic algorithms.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="crossover_prob">Crossover Probability:</label>
            <input id="crossover_prob" v-model="form.crossover_prob" v-tooltip="'Probability of mixing two individuals to create a new one.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="swapping_prob">Swapping Probability:</label>
            <input id="swapping_prob" v-model="form.swapping_prob" v-tooltip="'Probability of swapping genes during crossover.'" :class="inputClass"
                   type="number">
          </div>
        </div>
        <div v-if="form.optimizer === 'CmaEsSampler'">
          <!-- Inputs for settings specific to CmaEsSampler -->
          <div>
            <label for="sigma">Sigma:</label>
            <input id="sigma" v-model="form.sigma" v-tooltip="'Initial standard deviation of the CMA-ES distribution.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="n_startup_trials">Number of Startup Trials:</label>
            <input id="n_startup_trials" v-model="form.n_startup_trials" v-tooltip="'Initial number of trials for random sampling before optimization.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="consider_pruned_trials">Consider Pruned Trials:</label>
            <input id="consider_pruned_trials" v-model="form.consider_pruned_trials" v-tooltip="'Whether to include pruned trials in the estimation process.'" :class="inputClass"
                   type="checkbox">
          </div>
        </div>
        <div v-if="form.optimizer === 'NSGAIIISampler'">
          <!-- Inputs for settings specific to NSGAIIISampler -->
          <div>
            <label for="population_size">Population Size:</label>
            <input id="population_size" v-model="form.population_size" v-tooltip="'Number of individuals in each generation for genetic algorithms.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="crossover_prob">Crossover Probability:</label>
            <input id="crossover_prob" v-model="form.crossover_prob" v-tooltip="'Probability of mixing two individuals to create a new one.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="swapping_prob">Swapping Probability:</label>
            <input id="swapping_prob" v-model="form.swapping_prob" v-tooltip="'Probability of swapping genes during crossover.'" :class="inputClass"
                   type="number">
          </div>
        </div>
        <div v-if="form.optimizer === 'MOTPESampler'">
          <!-- Inputs for settings specific to MOTPESampler -->
          <div>
            <label for="consider_prior">Consider Prior:</label>
            <input id="consider_prior" v-model="form.consider_prior" v-tooltip="'Includes the prior in Gaussian Process. True makes the algorithm exploitation-oriented.'" :class="inputClass"
                   type="checkbox">
          </div>
          <div>
            <label for="prior_weight">Prior Weight:</label>
            <input id="prior_weight" v-model="form.prior_weight" v-tooltip="'The weight of the prior. Higher values prioritize past results.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="n_startup_trials">Number of Startup Trials:</label>
            <input id="n_startup_trials" v-model="form.n_startup_trials" v-tooltip="'Initial number of trials for random sampling before optimization.'" :class="inputClass"
                   type="number">
          </div>
          <div>
            <label for="n_ei_candidates">Number of EI Candidates:</label>
            <input id="n_ei_candidates" v-model="form.n_ei_candidates" v-tooltip="'Number of candidate samples for calculating Expected Improvement.'" :class="inputClass"
                   type="number">
          </div>
        </div>
        <div v-if="form.optimizer === 'QMCSampler'">
          <!-- Inputs for settings specific to QMCSampler -->
          <div>
            <label for="qmc_type">QMC Type:</label>
            <select id="qmc_type" v-model="form.qmc_type" v-tooltip="SelectedQMCType" :class="selectClass">
              <option value="sobol">Sobol</option>
              <option value="halton">Halton</option>
            </select>
          </div>
          <div>
            <label for="scramble">Scramble:</label>
            <input id="scramble" v-model="form.scramble" v-tooltip="'Scramble: Adds randomness to the sequence to improve statistical independence in simulations.'" :class="inputClass"
                   type="checkbox">
          </div>
        </div>
        <!-- Input fields for fitness ratios -->
        <!-- Checkbox to enable multivariate -->
        <div>
          <label for="isMultivariate">Multi-objective optimization</label>
          <input id="isMultivariate" v-model="form.isMultivariate" :class="inputClass" type="checkbox">
        </div>


        <div>
          <label for="fitnessMetric1">Fitness Metric 1:</label>
          <select id="fitnessMetric1" v-model="form.fitnessMetric1" v-tooltip="fitnessMetricDescription" :class="selectClass">
            <option value="Sharpe">Sharpe</option>
            <option value="Calmar">Calmar</option>
            <option value="Sortino">Sortino</option>
            <option value="Omega">Omega</option>
            <option value="Serenity">Serenity</option>
            <option value="SmartSharpe">Smart Sharpe</option>
            <option value="SmartSortino">Smart Sortino</option>
            <option value="MaxDrawdown">Max Drawdown</option>
            <option value="GrossLoss">Gross Loss</option>
          </select>
        </div>


        <div v-if="form.isMultivariate">
          <label for="fitnessMetric2">Fitness Metric 2:</label>
          <select id="fitnessMetric2" v-model="form.fitnessMetric2" v-tooltip="fitnessMetric2Description" :class="selectClass">
            <option value="Sharpe">Sharpe</option>
            <option value="Calmar">Calmar</option>
            <option value="Sortino">Sortino</option>
            <option value="Omega">Omega</option>
            <option value="Serenity">Serenity</option>
            <option value="SmartSharpe">Smart Sharpe</option>
            <option value="SmartSortino">Smart Sortino</option>
            <option value="MaxDrawdown">Max Drawdown</option>
            <option value="GrossLoss">Gross Loss</option>
          </select>

          <!-- New Dropdown for Second Objective Direction -->
          <div>
            <label for="secondObjectiveDirection">Second Objective Direction:</label>
            <select id="secondObjectiveDirection" v-model="form.secondObjectiveDirection" :class="selectClass">
              <option value="Maximize">Maximize</option>
              <option value="Minimize">Minimize</option>
            </select>
          </div>
        </div>
        <!-- Replicating the input forms from OptimizationTab.vue -->
        <!-- Fields for analysis session, shown after optimization -->
        <div v-if="optimizationComplete">
          <!-- Analysis session input fields -->
        </div>
        <Divider class="mt-16" title="Options"/>
        <div class="grid grid-cols-1 gap-6">
          <!-- logging mode -->
          <ToggleButton
            :object="form"
            name="debug_mode"
            title="Logging Mode"
            description="Each trials's training and testing metrics are logged into a csv file inside the 'storage/jesse-optuna' folder."/>
        </div>

        <!-- optimal_total -->
        <div class="select-none">
          <Divider class="mt-16" title="Optimal Trades"/>
          <p class="text-sm text-gray-500 dark:text-gray-400">
            The number that tells Jesse how many trades you would find optimal for your strategy in the targeted time
            period so that it can filter out those DNAs that cause too few trades.
            <a href="https://docs.jesse.trade/docs/optimize/executing-the-optimize-mode.html" target="_blank" class="font-semibold hover:underline italic">More Details...</a>
          </p>
          <br>
          <NumberInput title="Optimal number of trades:" name="optimal_total" :object="form"/>
        </div>

        <Divider class="mt-16" title="Duration"/>

        <div class="flex items-center select-none flex-1 mb-4">
          <input id="start_date"
                 v-model="form.start_date"
                 type="date"
                 name="start_date"
                 class="dark:bg-backdrop-dark dark:border-gray-900 dark:hover:bg-gray-700 hover:bg-gray-50 flex-1 cursor-pointer focus:ring-indigo-500 focus:border-indigo-500 dark:focus:border-indigo-400 flex justify-center items-center w-48 py-4 text-center sm:text-sm border-gray-300 rounded-l-md border-r-0"
          >

          <input id="finish_date"
                 v-model="form.finish_date"
                 type="date"
                 name="finish_date"
                 class="dark:border-gray-900 dark:hover:bg-gray-700 hover:bg-gray-50 dark:bg-backdrop-dark flex-1 cursor-pointer focus:ring-indigo-500 focus:border-indigo-500 dark:focus:border-indigo-400 flex justify-center items-center w-48 py-4 text-center sm:text-sm border-gray-300 rounded-r-md">
        </div>
      </div>
    </template>

    <template #right>
      <!-- Action Buttons -->
      <div v-if="!results.executing && !results.showResults">
        <div>
          <button class="flex justify-center items-center btn-primary text-center mb-4 w-full" @click="start($route.params.id)">
            <LightningBoltIcon class="w-5 h-5 mr-2" />
            Start
          </button>
          <div class="flex w-full">
            <button v-tooltip="'Resets all Optuna settings'" class="flex-1 my-button" @click="triggerModal(resetForm, 'Reset Settings', 'Are you sure you want to reset all Optuna settings?')">Reset Settings</button>
            <button v-tooltip="'Resets memory cache for all Optuna backtests'" class="flex-1 my-button" @click="triggerModal(resetCache, 'Clear Memory Cache', 'Are you sure you want to clear memory cache for all Optuna backtests?')">Clear Memory Cache</button>
            <button v-tooltip="'Clears all studies in the Optuna database'" class="flex-1 my-button" @click="triggerModal(resetDatabase, 'Clear Optuna Database', 'Are you sure you want to clear all studies in the Optuna database?')">Clear Optuna Database</button>
          </div>
        </div>
      </div>

      <!-- Execution -->
      <div v-if="results.executing && !results.showResults"
           class="flex flex-col items-center justify-center select-none"
      >
        <div class="mb-8 w-full">
          <button class="btn-cancel block mb-4 w-full" @click="cancel($route.params.id)">
            Cancel
          </button>
        </div>

        <div>
          <CircleProgressbar :progress="results.progressbar.current"/>
        </div>

        <h3 v-if="!results.exception.error" class="mt-8" v-text="remainingTimeText"/>
      </div>
      <!-- Show Results -->
      <div v-if="results.showResults">
        <div class="w-full">
          <button class="flex justify-center items-center btn-success text-center mb-4 w-full" @click="newSession">
            <ReplyIcon class="w-5 h-5 mr-2" />
            New session
          </button>
        </div>
        <div v-if="!results.conductingAnalysis" class="mb-4 w-full">
          <button class="flex justify-center items-center btn-primary text-center mb-4 w-full" @click="start($route.params.id,true,true,true)">
            <PresentationChartBarIcon class="w-5 h-5 mr-2" />
            Analysis of Best Trials
          </button>
        </div>
        <div v-if="!results.conductingAnalysis" style="width: 100%; display: flex; flex-direction: column; align-items: center;">
          <div style="margin-bottom: 10px; width: 100%; max-width: 400px; display: flex; flex-direction: column; align-items: center;">
            <label for="validationInterval">Backtesting Interval</label>
            <input id="validationInterval" v-model="form.validation_interval" v-tooltip="'Interval for segemented backtesting'" :class="inputClass"
                   type="number"
                   style="width: 100%;">
          </div>

          <div style="margin-bottom: 10px; width: 100%; max-width: 400px; display: flex; flex-direction: column; align-items: center;">
            <label for="robustTestIterationCount">Random Parameter Iterations</label>
            <input id="robustTestIterationCount" v-model="form.robust_test_iteration_count" v-tooltip="'Number of random parameter deviations to test'" :class="inputClass"
                   type="number"
                   style="width: 100%;">
          </div>

          <div style="margin-bottom: 10px; width: 100%; max-width: 400px; display: flex; flex-direction: column; align-items: center;">
            <label for="robustTestMax">Maximum Parameter Deviation</label>
            <input id="robustTestMax" v-model="form.robust_test_max" :class="inputClass" type="number"
                   style="width: 100%;">
          </div>

          <div style="margin-bottom: 10px; width: 100%; max-width: 400px; display: flex; flex-direction: column; align-items: center;">
            <label for="robustTestMin">Minimum Parameter Deviation</label>
            <input id="robustTestMin" v-model="form.robust_test_min" :class="inputClass" type="number"
                   style="width: 100%;">
          </div>

          <div style="margin-bottom: 10px; width: 100%; max-width: 400px; display: flex; flex-direction: column; align-items: center;">
            <label for="maxFinalNumberOfValidationResults">Maximum Number of Final Trials</label>
            <input id="maxFinalNumberOfValidationResults" v-model="form.max_final_number_of_validation_results" v-tooltip="'Maximum number of trials to generate a xlsx file for'" :class="inputClass"
                   type="number"
                   style="width: 100%;">
          </div>
          <div style="margin-bottom: 10px; width: 100%; max-width: 400px; display: flex; flex-direction: column; align-items: center;">
            <label for="optunaVisualizations">Optuna Plot Visualizations</label>
            <input id="optunaVisualizations" v-model="form.optuna_visualizations" v-tooltip="'Creates visualizations to display properties of the Optuna study'" :class="inputClass"
                   type="checkbox"
            >
          </div>
        </div>
        <div v-if="results.conductingAnalysis && !results.analysisFinished" class="mb-4 w-full">
          <button class="btn-cancel block mb-4 w-full" @click="cancel($route.params.id)">
            Cancel
          </button>
        </div>
        <div v-if="results.conductingAnalysis && !results.analysisFinished" class="mb-4">
          <div>
            <h3 class="text-lg font-semibold mb-2">Analysis in progress...</h3>
            <p class="text-sm text-gray-500 dark:text-gray-450">
              This may take a while depending on the number of trials in your optimization.
            </p>
          </div>
          <div v-if="results.progressString !== '' && !results.analysisFinished" class="flex flex-col items-center justify-center mt-2">
            <div>
              <p style="font-weight: 700;">{{ results.progressString }}</p>
            </div>
          </div>
        </div>
        <div v-if="results.analysisFinished" class="mb-4">
          <h1 class="text-lg font-semibold mb-2">xlsx files can be found in the './storage/optuna/validation' path.</h1>
        </div>
      </div>
      <button-confirm-modal
        :is-visible="isModalVisible"
        :title="modalTitle"
        :description="modalDescription"
        :on-confirm="pendingAction"
        @update:isVisible="isModalVisible = $event"
      />
    </template>
  </LayoutWithSidebar>
</template>

<script>
import { VTooltip } from 'floating-vue'
import 'floating-vue/dist/style.css'
import { mapActions, mapState, mapWritableState } from 'pinia'
import { useOptunaStore } from '@/stores/optuna'
import { LightningBoltIcon, ReplyIcon, PresentationChartBarIcon } from '@heroicons/vue/outline'
import LayoutWithSidebar from '@/layouts/LayoutWithSidebar'
import { useMainStore } from '@/stores/main'
import MultipleValuesTable from '@/components/MultipleValuesTable'
import Routes from '@/components/Routes'
import Divider from '@/components/Divider'
import CircleProgressbar from '@/components/Functional/CircleProgressbar'
import Exception from '@/components/Exception'
import helpers from '@/helpers'
import ToggleButton from '@/components/ToggleButton'
import ButtonConfirmModal from '@/components/Modals/ButtonConfirmModal'
import axios from 'axios'

export default {
  name: 'OptunaTab',
  components: {
    LayoutWithSidebar,
    Routes,
    Divider,
    CircleProgressbar,
    Exception,
    LightningBoltIcon,
    MultipleValuesTable,
    ReplyIcon,
    ToggleButton,
    ButtonConfirmModal,
    PresentationChartBarIcon
  },
  directives: {
    tooltip: VTooltip,
  },
  props: {
    form: {
      type: Object,
      required: true
    },
    results: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      copiedLogsInfo: false,
      isModalVisible: false, // Renamed for clarity
      modalTitle: '',
      modalDescription: '',
      pendingAction: null,
    }
  },
  computed: {
    ...mapWritableState(useMainStore, ['theme']),
    inputClass () {
      return this.theme === 'dark' ? 'input-dark' : 'input-light'
    },
    inputNumClass () {
      return this.theme === 'dark' ? 'input-num-dark' : 'input-num-light'
    },
    selectClass () {
      return this.theme === 'dark' ? 'select-dark' : 'select-light'
    },
    SelectedQMCType () {
      const descriptions = {
        sobol: 'Uses Sobol sequences for quasi-random number generation, offering better distribution properties.',
        halton: 'Employs Halton sequences, beneficial for higher-dimensional spaces.'
      }

      return descriptions[this.form.qmc_type] || 'Hover over an option to see its description.'
    },
    selectedSamplerDescription () {
      const descriptions = {
        GridSampler: 'Exhaustively searches a grid of parameters. Best for small, discrete spaces.',
        RandomSampler: 'Randomly selects parameters for each trial. Ideal for large spaces.',
        TPESampler: 'Uses probabilistic modeling for parameter selection, balancing exploration and exploitation.',
        CmaEsSampler: 'Optimizes non-linear functions by adapting a multivariate normal distribution.',
        NSGAIISampler: 'Evolves multi-objective solutions using genetic algorithms. Finds Pareto-optimal set.',
        NSGAIIISampler: 'Tailored for many-objective problems, ensuring diverse, well-distributed solutions.',
        MOTPESampler: 'Extends TPE for multi-objective optimization, balancing multiple goals.',
        QMCSampler: 'Utilizes low-discrepancy sequences for more uniform parameter sampling.',
        BruteForceSampler: 'Methodically evaluates every parameter combination. Guarantees global optimum.'
      }

      return descriptions[this.form.optimizer] || 'Hover over an option to see its description.'
    },
    fitnessMetricDescription () {
      const metricDescriptions = {
        Sharpe: 'Measures excess return per unit of deviation in an investment.',
        Calmar: 'Compares average annual compounded rate of return and maximum drawdown risk.',
        Sortino: 'Similar to Sharpe, but focuses only on downside deviation.',
        Omega: 'Considers both upside and downside performance for a better risk-return profile.',
        Serenity: 'Advanced metric incorporating drawdown, volatility, and return.',
        SmartSharpe: 'A modified Sharpe ratio that adjusts for skewness and kurtosis.',
        SmartSortino: 'A modified Sortino ratio considering higher moments of distribution.',
        MaxDrawdown: 'Largest peak-to-trough decline in investment value.',
        GrossLoss: 'Total sum of all losing trades in the backtest.'
      }

      const objective = 'Maximized'
      const description = metricDescriptions[this.form.fitnessMetric1] || 'Select a metric to see its description.'

      return `${description} (${objective} objective)`
    },

    fitnessMetric2Description () {
      const metricDescriptions = {
        Sharpe: 'Measures excess return per unit of deviation in an investment.',
        Calmar: 'Compares average annual compounded rate of return and maximum drawdown risk.',
        Sortino: 'Similar to Sharpe, but focuses only on downside deviation.',
        Omega: 'Considers both upside and downside performance for a better risk-return profile.',
        Serenity: 'Advanced metric incorporating drawdown, volatility, and return.',
        SmartSharpe: 'A modified Sharpe ratio that adjusts for skewness and kurtosis.',
        SmartSortino: 'A modified Sortino ratio considering higher moments of distribution.',
        MaxDrawdown: 'Largest peak-to-trough decline in investment value.',
        GrossLoss: 'Total sum of all losing trades in the backtest.'
      }

      const objective = this.form.secondObjectiveDirection === 'Maximize' ? 'Maximized' : 'Minimized'
      const description = metricDescriptions[this.form.fitnessMetric2] || 'Select a metric to see its description.'

      return `${description} (${objective} objective)`
    },

    hasExecutedTrades () {
      return this.results.metrics.length > 0
    },
    ...mapState(useMainStore, ['isInitiated', 'baseURL']),
    auth_key () {
      return sessionStorage.auth_key
    },
    remainingTimeText () {
      return helpers.remainingTimeText(this.results.progressbar.estimated_remaining_seconds)
    },

  },
  watch: {
    form: {
      handler () {
        helpers.localStorageSet('optunaForm', this.form)
      },
      deep: true
    },
  },
  methods: {
    triggerModal (action, title, description) {
      this.modalTitle = title
      this.modalDescription = description
      this.pendingAction = action
      this.isModalVisible = true
    },
    resetCache () {
      axios.post('/optuna', { id: 'optuna', string: 'reset_cache' })
        .then(response => console.log(response))
        .catch(error => console.log(error))
    },

    resetDatabase () {
      axios.post('/optuna', { id: 'optuna', string: 'reset_database' })
        .then(response => console.log(response))
        .catch(error => console.log(error))
    },
    resetForm () {
      const form = JSON.parse(localStorage.getItem('optunaForm')) || {}
      const defaultValues = {
        debug_mode: false,
        optimal_total: 50,
        optimizer: 'NSGAIISampler',
        fitnessMetric1: 'Sharpe',
        fitnessMetric2: 'MaxDrawdown',
        isMultivariate: false,
        secondObjectiveDirection: 'Minimize',
        consider_prior: true,
        prior_weight: 1.0,
        n_startup_trials: 10,
        n_ei_candidates: 24,
        group: false,
        sigma: 0.5,
        consider_pruned_trials: false,
        population_size: 500,
        crossover_prob: 0.9,
        swapping_prob: 0.5,
        qmc_type: 'sobol',
        scramble: true,
      }

      Object.keys(defaultValues).forEach(key => {
        form[key] = defaultValues[key]
      })

      localStorage.setItem('optunaForm', JSON.stringify(form))
      location.reload()
    },
    ...mapActions(useOptunaStore, ['addTab', 'startInNewTab', 'start', 'cancel', 'rerun', 'newOptuna']),
    copyInfoLogs () {
      const infoLogsToCopy = document.querySelector('#copy-info-logs')
      infoLogsToCopy.setAttribute('type', 'text')
      infoLogsToCopy.select()
      document.execCommand('copy')
      this.copiedLogsInfo = true

      // unselect the range
      infoLogsToCopy.setAttribute('type', 'hidden')
      window.getSelection().removeAllRanges()

      setTimeout(() => {
        this.copiedLogsInfo = false
      }, 3000)
    },
    newSession () {
      this.results.showResults = false
      this.results.executing = false
      this.results.progressbar.current = 0
      this.results.progressbar.estimated_remaining_seconds = 0
      this.results.alert.message = ''
      this.results.alert.type = ''
    }
  }
}
</script>

