<template>
  <Tabs v-if="false" :page-id="pageId" :tabs="tabs" />

  <OptunaTab v-if="tabs[pageId]" :form="tabs[pageId].form" :results="tabs[pageId].results" :modals="tabs[pageId].modals" />
</template>

<script>
import { mapWritableState } from 'pinia'
import { useOptunaStore } from '@/stores/optuna'
import OptunaTab from '@/views/tabs/OptunaTab'
import Tabs from '@/components/Tabs'

export default {
  name: 'Optuna',
  components: {
    Tabs,
    OptunaTab,
  },
  computed: {
    ...mapWritableState(useOptunaStore, [
      'tabs'
    ]),
    pageId () {
      return parseInt(this.$route.params.id)
    },
  },
}
</script>
