<template>
  <div v-if="isVisible" class="modal-overlay">
    <div class="modal bg-white dark:bg-gray-800 rounded-lg shadow-xl">
      <div class="modal-header">
        <div class="modal-title-spacer"/> <!-- Spacer -->
        <h3 class="modal-title text-red-600 dark:text-red-400 font-semibold">{{ title }}</h3>
        <button class="close-button" @click="closeModal">x</button>
      </div>
      <div class="modal-body">
        <p class="text-gray-500 dark:text-gray-300">{{ description }}</p>
      </div>
      <div class="modal-actions">
        <button class="bg-red-600 text-white rounded px-4 py-2 hover:bg-red-700" @click="confirmAction">Confirm</button>
        <button class="bg-gray-300 text-gray-700 rounded px-4 py-2 hover:bg-gray-400" @click="closeModal">Cancel</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ButtonConfirmModal',
  props: {
    isVisible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: 'Confirm Action'
    },
    description: {
      type: String,
      default: 'Are you sure you want to proceed?'
    },
    onConfirm: {
      type: Function,
      default: () => {}
    }
  },
  emits: ['update:isVisible'],
  methods: {
    closeModal () {
      this.$emit('update:isVisible', false)
    },
    confirmAction () {
      if (this.onConfirm) {
        this.onConfirm()
      }
      this.closeModal()
    }
  }
}
</script>

<style scoped>

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  padding: 20px;
  max-width: 500px;
  width: 90%;
  background: #fff;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-title-spacer {
  flex: 1; /* Takes up space equivalent to the close button */
}

.modal-title {
  margin: 0;
  flex: 2; /* Allows the title to be centered */
  text-align: center;
  font-size: 1.5em;
}

.close-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 20px;
  color: #777;
  flex: 1;
  text-align: right;
}

.modal-body {
  margin-top: 10px;
  text-align: center;
}

.modal-actions {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

button:hover {
  opacity: 0.8;
}

.confirm-button {
  background-color: #4CAF50;
  color: white;
}

.cancel-button {
  background-color: #f44336;
  color: white;
}
</style>

