<template>
  <!-- Debugger Logs -->
  <SlideOver v-if="form.debug_mode"
             name="logsModal"
             :object="results"
             title="Logs">
    <template #default>
      <Logs :logs="results.infoLogs"/>
    </template>

    <template #buttons>
      <button
        class="btn-nav"
        @click="copyInfoLogs">
        <CheckIcon v-if="copiedLogsInfo" class="h-6 w-6" aria-hidden="true"/>
        <ClipboardIcon v-if="!copiedLogsInfo && results.infoLogs.length != 0" class="h-6 w-6" aria-hidden="true"/>
      </button>
      <input id="copy-info-logs" type="hidden" :value="results.infoLogs">
    </template>
  </SlideOver>

  <!-- Execution -->
  <div v-if="results.executing && !results.showResults"
       class="flex flex-col items-center justify-center select-none mt-[10%]"
  >
    <div>
      <CircleProgressbar :progress="results.progressbar.current"/>
    </div>

    <h3 v-if="!results.exception.error" class="mt-8 animate-pulse" v-text="remainingTimeText"/>

    <div class="mt-8">
      <button data-cy="backtest-cancel-button" class="flex justify-center items-center btn-cancel mb-4 w-64"
              @click="cancel($route.params.id)">
        <BanIcon class="w-5 h-5 mr-2"/>
        Cancel
      </button>

      <a v-if="form.debug_mode && results.exception.error && results.progressbar.current !== 0"
         :href="logsUrl"
         class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
        <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
        Debugging Logs
      </a>
    </div>

    <!-- exception  -->
    <div v-if="results.exception.error && results.executing" class="mx-auto container mt-8">
      <Exception :title="results.exception.error"
                 :content="results.exception.traceback"
                 mode="backtest"
                 :debug-mode="form.debug_mode"
                 :session-id="results.generalInfo.session_id"/>
    </div>
  </div>

  <LayoutWithSidebar else>
    <template #left>
      <!-- alert -->
      <div v-if="results.showResults && results.alert.message">
        <Alert :data="results.alert"/>
      </div>

      <!-- Content -->
      <div v-if="!results.executing && !results.showResults" data-cy="backtest-content-section">
        <Routes v-if="isInitiated" :form="form" :results="results" @update-strategy-name="handleStrategyNameUpdate"
        />
        <!-- Hyperparameters Section -->
        <div v-if="results.shownHyperparameters && results.shownHyperparameters.length" class="hyperparameters-section">
          <h3 style="text-align: center; font-size: 19px; margin-top: 10px; margin-bottom: 20px;">Strategy Parameters</h3>
          <ul style="padding: 0;">
            <li v-for="(param, index) in results.shownHyperparameters" :key="index" style="display: flex; align-items: center; margin-bottom: 10px;">
              <label :for="'param-' + index" style="font-weight: 700; margin-right: 10px;">{{ param.name }}:</label>

              <!-- Input for numbers (int and float) -->
              <input
                v-if="param.type === 'int' || param.type === 'float'"
                :id="'param-' + index"
                v-model="param.default"
                :class="inputClass"
                :type="getParamInputType(param.type)"
                style="flex-grow: 0; width: 20%;"
                @input="onParameterChange"
              >

              <!-- Dropdown for strings -->
              <select v-else-if="param.type === 'str'"
                      :id="'param-' + index"
                      v-model="param.default"
                      :class="inputClass"
                      style="flex-grow: 0; width: 20%;"
                      @change="onParameterChange">
                <option v-for="option in param.list" :key="option" :value="option">{{ option }}</option>
              </select>

              <!-- Dropdown for booleans -->
              <select v-else-if="param.type === 'bool'"
                      :id="'param-' + index"
                      v-model="param.default"
                      :class="inputClass"
                      style="flex-grow: 0; width: 20%;"
                      @change="onBooleanChange(param, $event)">
                <option value="true">True</option>
                <option value="false">False</option>
              </select>
            </li>
          </ul>
          <div class="flex justify-center">
            <button
              v-if="isParametersModified"
              v-tooltip="{ content: 'Warning: Hashtag python commments are not preserved', class: 'red-tooltip' }"
              class="flex items-center bg-green-500 text-white text-md py-2 px-4 rounded mt-4 hover:bg-green-600"
              @click="updateStrategy"
            >
              <PencilAltIcon class="w-5 h-5 mr-2"/>
              Update Strategy
            </button>
          </div>
        </div>
        <!-- Parameter Evaluation Divider -->
        <Divider v-if="results.shownHyperparameters && results.shownHyperparameters.length === 2" class="mt-16" title="Parameter Evaluation"/>

        <!-- Button and Status Container -->
        <div v-if="results.shownHyperparameters && results.shownHyperparameters.length === 2" class="text-center">
          <!-- Start Button -->
          <button v-if="!results.image_status"
                  v-tooltip="'Conduct backtests over all possible parameter combinations for the hyperparameter ranges to generate plots showing profitable parameter combinations.'"
                  class="flex items-center justify-center btn-purple text-center mb-4 w-full"
                  @click="eval_param_start($route.params.id)">
            <LightningBoltIcon class="w-5 h-5 mr-2"/>
            Start
          </button>
          <button v-if="results.images && results.image_status === 'finished'"
                  v-tooltip="'Conduct backtests over all possible parameter combinations for the hyperparameter ranges to generate plots showing profitable parameter combinations.'"
                  class="flex items-center justify-center btn-success text-center mb-4 w-full"
                  @click="eval_param_start($route.params.id)">
            <ReplyIcon class="w-5 h-5 mr-2"/>
            Start again
          </button>
          <button v-if="results.image_status && results.image_status !== 'finished'" data-cy="backtest-cancel-button" class="flex justify-center items-center btn-cancel text-center mb-4 w-full"
                  @click="cancel($route.params.id)">
            <BanIcon class="w-5 h-5 mr-2"/>
            Cancel
          </button>
          <!-- Status Text -->
          <div v-if="results.image_status && results.image_status !== 'finished'" :class="{'red-text': results.image_status === 'Exception occurred'}"
               class="status-text" style="margin-top: 10px; font-size: 1.25rem; font-weight: bold;">
            {{ results.image_status }}
          </div>
          <div v-if="results.image_status === 'finished'" class="status-text" style="margin-top: 10px; font-size: 1.25rem; font-weight: bold;">
            {{ formatRoute(results.route_copy[0]) }}
          </div>
          <!-- Images Display Container -->
          <div v-if="results.images && results.images.length">
            <input v-model="imageWidth" type="range" min="100" max="1800" >
            <span>{{ imageWidth }}px</span>
          </div>
          <div v-if="results.images && results.images.length" class="images-container my-4">
            <img v-for="(imageObject, index) in results.images" :key="index" :src="imageObject.src" class="resizable-images my-2"
                 :style="{ width: imageWidth + 'px' }">
          </div>
        </div>
        <Divider class="mt-16" title="Options"/>

        <div data-cy="backtest-option-section" class="grid grid-cols-1 gap-6">
          <!-- debug mode -->
          <ToggleButton
            :object="form"
            name="debug_mode"
            title="Debug Mode"
            description="Logs every step of the execution. Very helpful for debugging your strategy but takes a lot longer to execute"/>

          <!-- export chart -->
          <ToggleButton
            :object="form"
            name="export_chart"
            title="Export Charts"
            description="Exports charts for your portfolio's daily balance."/>

          <!-- export trading view -->
          <ToggleButton
            :object="form"
            name="export_tradingview"
            title="Export Tradingview"
            description="Exports the executed trades in a format accepted by TradingView's Pine Editor. Useful to look at the executed orders on their chart."/>

          <!-- export full reports -->
          <ToggleButton
            :object="form"
            name="export_full_reports"
            title="Export QuantStats reports"
            description="Exports a HTML file generated by QuantStats library."/>

          <!-- export backtesting chart -->
          <ToggleButton
            :object="form"
            name="export_backtesting_chart"
            title="Export Backtesting Chart"
            description="Exports a chart with candlesticks and plotted indicators."/>

          <!-- export correlation table -->
          <ToggleButton
            :object="form"
            name="export_correlation_table"
            title="Export Correlation Table"
            description="Exports a table with correlation between assets."/>

          <!-- export csv -->
          <ToggleButton
            :object="form"
            name="export_csv"
            title="Export CSV"
            description="Exports executed trades in a CSV format"/>

          <!-- export json -->
          <ToggleButton
            :object="form"
            name="export_json"
            title="Export JSON"
            description="Exports executed trades in a JSON format"/>
        </div>

        <Divider class="mt-16" title="Duration"/>

        <div class="flex items-center select-none flex-1 mb-4">
          <input id="start_date"
                 v-model="form.start_date"
                 data-cy="backtest-start-date"
                 type="date"
                 name="start_date"
                 class="dark:bg-backdrop-dark dark:border-gray-900 dark:hover:bg-gray-700 hover:bg-gray-50 flex-1 cursor-pointer focus:ring-indigo-500 focus:border-indigo-500 dark:focus:border-indigo-400 flex justify-center items-center w-48 py-4 text-center sm:text-sm border-gray-300 rounded-l-md border-r-0"
          >

          <input id="finish_date"
                 v-model="form.finish_date"
                 data-cy="backtest-finish-date"
                 type="date"
                 name="finish_date"
                 class="dark:border-gray-900 dark:hover:bg-gray-700 hover:bg-gray-50 dark:bg-backdrop-dark flex-1 cursor-pointer focus:ring-indigo-500 focus:border-indigo-500 dark:focus:border-indigo-400 flex justify-center items-center w-48 py-4 text-center sm:text-sm border-gray-300 rounded-r-md">
        </div>
      </div>

      <!-- Results -->
      <div v-if="results.showResults"
           class="w-full mx-auto">
        <div>
          <Divider title="Routes"/>
          <MultipleValuesTable :data="results.routes_info" header/>

          <Divider v-if="results.hyperparameters.length" class="mt-16" title="Hyperparameters"/>
          <KeyValueTable v-if="results.hyperparameters.length" :data="results.hyperparameters"/>

          <Divider v-if="hasExecutedTrades" class="mt-16" title="Equity Curve"/>
          <EquityCurve v-if="hasExecutedTrades" :equity-curve="results.charts.equity_curve"/>

          <Divider v-if="hasExecutedTrades" class="mt-16" title="Performance"/>
          <KeyValueTable v-if="hasExecutedTrades" :data="results.metrics"/>

          <div v-if="!hasExecutedTrades"
               class="text-yellow-500 border-yellow-400 bg-yellow-50 dark:bg-gray-700 dark:text-yellow-400 mt-16 text-center text-2xl rounded border-2 border-dashed dark:border-gray-800 py-16 select-none"
          >
            No trades were executed via this strategy!
          </div>
        </div>
      </div>
    </template>

    <template #right>
      <!-- Action Buttons -->
      <div v-if="!results.executing">
        <div v-if="results.showResults">
          <button class="flex justify-center items-center btn-primary text-center mb-4 w-full"
                  @click="rerun($route.params.id)">
            <RefreshIcon class="w-5 h-5 mr-2"/>
            Rerun
          </button>

          <button class="flex justify-center items-center btn-success text-center mb-4 w-full"
                  @click="newBacktest($route.params.id)">
            <ReplyIcon class="w-5 h-5 mr-2"/>
            New session
          </button>

          <a v-if="form.debug_mode"
             :href="logsUrl"
             target="_blank"
             class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
            <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
            Debugging Logs
          </a>

          <a v-if="form.export_chart && hasExecutedTrades"
             :href="legacyChartUrl"
             target="_blank"
             class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
            <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
            Legacy Chart
          </a>

          <a v-if="form.export_full_reports && hasExecutedTrades"
             :href="fullReportsUrl"
             target="_blank"
             class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
            <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
            QuantStats Report
          </a>
          <a v-if="form.export_backtesting_chart && hasExecutedTrades"
             :href="backtestingChartUrl"
             target="_blank"
             class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
            <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
            Backtesting Chart
          </a>
          <a v-if="form.export_correlation_table && hasExecutedTrades"
             :href="correlationTableUrl"
             target="_blank"
             class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
            <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
            Correlation Table
          </a>
          <a v-if="form.export_csv && hasExecutedTrades"
             :href="csvUrl"
             target="_blank"
             class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
            <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
            CSV
          </a>

          <a v-if="form.export_json && hasExecutedTrades"
             :href="jsonUrl"
             target="_blank"
             class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
            <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
            JSON
          </a>

          <a v-if="form.export_tradingview && hasExecutedTrades"
             :href="tradingviewUrl"
             target="_blank"
             class="flex justify-center items-center btn-secondary text-center mb-4 w-full">
            <DocumentDownloadIcon class="w-5 h-5 mr-2"/>
            TradingView Pine Editor
          </a>

          <hr class="my-8 border-2 dark:border-gray-600 rounded-full">

          <KeyValueTableSimple :data="results.info"/>
        </div>

        <div v-else>
          <button data-cy="start-button" class="flex items-center justify-center btn-primary text-center mb-4 w-full"
                  @click="start($route.params.id)">
            <LightningBoltIcon class="w-5 h-5 mr-2"/>
            Start
          </button>

          <button data-cy="start-new-tab-button"
                  class="flex items-center justify-center btn-secondary text-center mb-4 w-full"
                  @click="startInNewTab($route.params.id)">
            <PlusSmIcon class="w-5 h-5 mr-2"/>
            Start in a new tab
          </button>
        </div>
      </div>
    </template>
  </LayoutWithSidebar>
</template>

<script>
import { VTooltip } from 'floating-vue'
import 'floating-vue/dist/style.css'
import { mapActions, mapState, mapWritableState } from 'pinia'
import { useBacktestStore } from '@/stores/backtest'
import Logs from '@/components/Logs'
import LayoutWithSidebar from '@/layouts/LayoutWithSidebar'
import MultipleValuesTable from '@/components/MultipleValuesTable'
import { useMainStore } from '@/stores/main'
import { ClipboardIcon, CheckIcon } from '@heroicons/vue/solid'
import {
  LightningBoltIcon,
  PlusSmIcon,
  RefreshIcon,
  ReplyIcon,
  DocumentDownloadIcon,
  BanIcon,
  PencilAltIcon
} from '@heroicons/vue/outline'
import SlideOver from '@/components/Functional/SlideOver'
import ToggleButton from '@/components/ToggleButton'
import helpers from '@/helpers'
import Divider from '@/components/Divider'
import axios from '@/http'

export default {
  name: 'BacktestTab',
  components: {
    LayoutWithSidebar,
    Logs,
    ToggleButton,
    MultipleValuesTable,
    ClipboardIcon,
    CheckIcon,
    SlideOver,
    LightningBoltIcon,
    PlusSmIcon,
    RefreshIcon,
    ReplyIcon,
    DocumentDownloadIcon,
    BanIcon,
    Divider,
    PencilAltIcon,
  },
  directives: {
    tooltip: VTooltip,
  },
  props: {
    form: {
      type: Object,
      required: true
    },
    results: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      imageWidth: 800,
      strategyName: '',
      showNestedButtons: false,
      originalHyperparameters: [],
      copiedLogsInfo: false,
      svgObject: { display: false },
      isParametersModified: false,
    }
  },
  computed: {
    ...mapWritableState(useMainStore, ['theme']),
    inputClass () {
      return this.theme === 'dark' ? 'input-dark' : 'input-light'
    },
    legacyChartUrl () {
      let url = `/download/backtest/chart/${this.results.generalInfo.session_id}?token=${this.auth_key}`
      if (this.baseURL !== '/') {
        url = this.baseURL + url
      }
      return url
    },
    logsUrl () {
      let url = `/download/backtest/log/${this.results.generalInfo.session_id}?token=${this.auth_key}`
      if (this.baseURL !== '/') {
        url = this.baseURL + url
      }
      return url
    },
    tradingviewUrl () {
      let url = `/download/backtest/tradingview/${this.results.generalInfo.session_id}?token=${this.auth_key}`
      if (this.baseURL !== '/') {
        url = this.baseURL + url
      }
      return url
    },
    fullReportsUrl () {
      let url = `/download/backtest/full-reports/${this.results.generalInfo.session_id}?token=${this.auth_key}`
      if (this.baseURL !== '/') {
        url = this.baseURL + url
      }
      return url
    },
    backtestingChartUrl () {
      let url = `/download/backtest/backtesting-chart/${this.results.generalInfo.session_id}?token=${this.auth_key}`
      if (this.baseURL !== '/') {
        url = this.baseURL + url
      }
      return url
    },
    correlationTableUrl () {
      let url = `/download/backtest/correlation-table/${this.results.generalInfo.session_id}?token=${this.auth_key}`
      if (this.baseURL !== '/') {
        url = this.baseURL + url
      }
      return url
    },
    csvUrl () {
      let url = `/download/backtest/csv/${this.results.generalInfo.session_id}?token=${this.auth_key}`
      if (this.baseURL !== '/') {
        url = this.baseURL + url
      }
      return url
    },
    jsonUrl () {
      let url = `/download/backtest/json/${this.results.generalInfo.session_id}?token=${this.auth_key}`
      if (this.baseURL !== '/') {
        url = this.baseURL + url
      }
      return url
    },
    hasExecutedTrades () {
      return this.results.metrics.length > 0
    },
    ...mapState(useMainStore, ['isInitiated', 'baseURL']),
    auth_key () {
      return sessionStorage.auth_key
    },
    remainingTimeText () {
      return helpers.remainingTimeText(this.results.progressbar.estimated_remaining_seconds)
    }
  },
  watch: {
    form: {
      handler () {
        helpers.localStorageSet('backtestForm', this.form)
      },
      deep: true
    },
    // Watch for changes in the hyperparameters prop and update the original state
    'results.shownHyperparameters': {
      immediate: true,
      handler (newVal) {
        this.originalHyperparameters = JSON.parse(JSON.stringify(newVal))
      }
    },
  },
  methods: {
    formatRoute (route) {
      return `${route.exchange}, ${route.symbol}, ${route.timeframe}, ${route.strategy}`
    },
    currentTheme () {
      if (localStorage.theme === 'dark' || (!('theme' in localStorage) && window.matchMedia('(prefers-color-scheme: dark)').matches)) {
        return 'dark'
      } else {
        return 'light'
      }
    },
    toggleNestedButtons () {
      this.showNestedButtons = !this.showNestedButtons
    },
    onBooleanChange (param, event) {
    // Convert the string value to a boolean
      param.default = event.target.value === 'true'
      this.onParameterChange()
    },
    handleStrategyNameUpdate (newStrategyName) {
      this.strategyName = newStrategyName
    },
    onParameterChange () {
      this.isParametersModified = !this.areParametersUnchanged()
    },

    areParametersUnchanged () {
      return this.results.shownHyperparameters.every((param, index) => {
        const originalParam = this.originalHyperparameters[index]
        if (typeof param.default === 'object') {
        // Perform deep comparison for objects and arrays
          return JSON.stringify(param.default) === JSON.stringify(originalParam.default)
        }
        return param.default === originalParam.default
      })
    },
    updateStrategy () {
      const pathSegmentInfo = this.getCurrentPathSegment()
      const payload = this.results.shownHyperparameters.reduce((acc, param) => {
        // Convert float values to strings to preserve the decimal part
        if (param.type === 'float') {
          acc[param.name] = param.default.toString()
        } else {
          acc[param.name] = param.default
        }
        return acc
      }, {})

      axios.put('/strategy-hyperparameters', {
        id: pathSegmentInfo.segment,
        current_page: pathSegmentInfo.number,
        strategy_name: this.strategyName,
        hyperparameters: payload,
      })
        .then(response => {
          this.isParametersModified = false
        })
        .catch(error => {
          console.error(error.response.data)
        })

      this.isParametersModified = false
      // Update the original hyperparameters to the new state
      this.originalHyperparameters = JSON.parse(JSON.stringify(this.results.shownHyperparameters))
    },
    getCurrentPathSegment () {
    // Split the URL path by '/' and filter out empty strings
      const pathSegments = this.$route.path.split('/').filter(Boolean)

      // Check if there are at least two segments (e.g., 'backtest' and '1')
      if (pathSegments.length >= 2) {
      // Return an object with both the segment and the number
        return {
          segment: pathSegments[0], // e.g., 'backtest'
          number: pathSegments[1] // e.g., '1'
        }
      } else if (pathSegments.length === 1) {
      // If there's only one segment, return it without the number
        return {
          segment: pathSegments[0],
          number: String(1)
        }
      }
    },
    getParamInputType (type) {
      if (type === 'int') {
        return 'number'
      } else if (type === 'float') {
        return 'text' // HTML does not have a 'float' type, so use 'text' and validate/format as needed
      } else { // Default to 'text' for string and other types
        return 'text'
      }
    },
    ...mapActions(useBacktestStore, ['addTab', 'startInNewTab', 'start', 'eval_param_start', 'cancel', 'rerun', 'newBacktest']),
    copyInfoLogs () {
      const infoLogsToCopy = document.querySelector('#copy-info-logs')
      infoLogsToCopy.setAttribute('type', 'text')
      infoLogsToCopy.select()
      document.execCommand('copy')
      this.copiedLogsInfo = true

      // unselect the range
      infoLogsToCopy.setAttribute('type', 'hidden')
      window.getSelection().removeAllRanges()

      setTimeout(() => {
        this.copiedLogsInfo = false
      }, 3000)
    },
  },

}
</script>

<style scoped>
.status-text {
  /* Additional styling for readability and centering of the status text */
  color: #333; /* Adjust color as needed */
  padding: 10px;
  border-radius: 5px;
  background-color: #f0f0f0; /* Adjust background color as needed */
}

.images-container img {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  margin-left: auto;
  margin-right: auto;
}

.resizable-image {
  max-width: 100%; /* Ensures image is not larger than its container */
  height: auto; /* Maintains aspect ratio */
  resize: both; /* Allows resizing */
  overflow: auto; /* Adds scrollbars if resized smaller than image */
  display: block; /* Correctly positions the image */
  margin: 10px auto; /* Centers the image with margin */
}

.input-light {
  background-color: white;
  color: black;
  /* Add other styles for light theme */
}

.input-dark {
  background-color: #333; /* Dark gray */
  color: #fff; /* White text */
  border: 1px solid #555; /* Slightly lighter border for visibility */
  /* Add other styling as needed */
}

.red-tooltip {
  color:#8B0000;
}
</style>
