  created () {
    this.initiate()
  },
  beforeUnmount () {
    window.removeEventListener('resize', this.handleResize)
    document.removeEventListener('keydown', this.handleKeyPress)
  },
  mounted () {
    // Check if the component is loaded in the context where you need to run the handler
    if (this.$route.path.includes('backtest')) {
      // Manually invoke the handler for 'form.routes'
      if (this.form.routes.length > 0) {
        const lastStrategy = this.form.routes[this.form.routes.length - 1].strategy
        this.sendStrategyToBackend(lastStrategy)
      }
    }
    window.addEventListener('resize', this.handleResize)
    this.handleResize() // Call it initially to set the size
  },
  methods: {
    toggleUtilityBox () {
      this.showUtilityBox = !this.showUtilityBox
    },
    resetSettings () {
      // Reset chart settings to default
      this.chartInstance.applyOptions(this.chartProperties)
      // Reset other settings as needed
    },
    resetYScale () {
      // Reset the price scale to default
      this.chartInstance.priceScale().applyOptions({
        autoScale: true,
      })
    },
    toggleNightMode () {
      this.isNightMode = !this.isNightMode
      const backgroundColor = this.isNightMode ? '#000000' : '#FFFFFF'
      const textColor = this.isNightMode ? '#FFFFFF' : '#000000'
      const volumeColor = this.isNightMode ? 'rgba(122, 168, 228, 0.55)' : 'rgba(4, 111, 232, 0.25)'

      this.chartInstance.applyOptions({
        layout: {
          backgroundColor: backgroundColor,
          textColor: textColor,
        },
      })

      if (this.volumeSeries) {
        this.volumeSeries.applyOptions({ color: volumeColor })
      }

      // Toggle the moon/sun icon
      this.moonIcon = this.isNightMode ? faSun : faMoon
    },
    toggleFibonacciRetracement () {
      this.isFibonacciRetracement = !this.isFibonacciRetracement
      if (!this.isFibonacciRetracement) {
        // Remove all Fibonacci lines
        this.fibonacciLines.forEach(line => this.candlestickSeries.removePriceLine(line))
        this.fibonacciLines = []
      } else {
        this.drawFibonacciRetracement()
      }
    },
    drawFibonacciRetracement () {
      if (!this.isFibonacciRetracement) return

      // Check if candlestickData is defined and has length
      if (!this.candlestickData || this.candlestickData.length === 0) {
        return
      }

      // Get the visible range of the chart
      const visibleRange = this.chartInstance.timeScale().getVisibleRange()

      // Filter the candle data to get the data within the visible range
      const visibleCandleData = this.candlestickData.filter(candle =>
        candle.time >= visibleRange.from && candle.time <= visibleRange.to
      )

      // Calculate the high and low within the visible range
      let high = -Infinity
      let low = Infinity
      visibleCandleData.forEach(candle => {
        if (candle.high > high) high = candle.high
        if (candle.low < low) low = candle.low
      })

      // Calculate Fibonacci levels and titles
      const fibonacciLevels = [
        { level: 0, title: '0%' },
        { level: 0.236, title: '23.6%' },
        { level: 0.382, title: '38.2%' },
        { level: 0.5, title: '50%' },
        { level: 0.618, title: '61.8%' },
        { level: 0.786, title: '78.6%' },
        { level: 1, title: '100%' }
      ]

      fibonacciLevels.forEach(({ level, title }, index) => {
        const priceLevel = low + (high - low) * level

        // Check if a line for this level already exists
        let fibonacciLine = this.fibonacciLines[index]

        if (fibonacciLine) {
          // Update the price of the existing line
          fibonacciLine.applyOptions({ price: priceLevel })
        } else {
          // Create a new line for this level
          fibonacciLine = this.candlestickSeries.createPriceLine({
            price: priceLevel,
            color: 'rgba(144, 238, 144, 0.5)',
            lineWidth: 2,
            lineStyle: 0,
            title: title,
            axisLabelVisible: true
          })
          this.fibonacciLines[index] = fibonacciLine
        }
      })

      // Remove any extra lines if the number of levels has decreased
      if (this.fibonacciLines.length > fibonacciLevels.length) {
        const linesToRemove = this.fibonacciLines.slice(fibonacciLevels.length)
        linesToRemove.forEach(line => this.candlestickSeries.removePriceLine(line))
        this.fibonacciLines = this.fibonacciLines.slice(0, fibonacciLevels.length)
      }
    },
    rgbaToHex (rgba) {
      const [r, g, b] = rgba.match(/\d+/g).map(Number)
      return `#${((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1)}`
    },
    updateVolumeSeriesColor () {
      console.log(this.volumeSeries)
      this.volumeSeries.applyOptions({ color: this.volumeColor })
      this.candlestickSeries.applyOptions({ Color: this.volumeColor })
      /*       if (this.chartInstance && this.volumeSeries) {
        const opacity = 0.3
        const rgbaColor = this.convertHexToRGBA(this.volumeColor, opacity)

        // Remove the existing volume series
        this.chartInstance.removeSeries(this.volumeSeries)

        // Create a new volume series with the updated color
        this.volumeSeries = this.chartInstance.addHistogramSeries({
          color: rgbaColor,
          priceFormat: {
            type: 'volume',
          },
          priceScaleId: 'volume',
          scaleMargins: {
            top: 0.7,
            bottom: 0,
          },
        })

        // Set the data for the new volume series
        this.volumeSeries.setData(this.volumeData)

        // Apply visibility options to the new volume series
        this.volumeSeries.applyOptions({ visible: this.volumeSeriesVisible })

        // Update the price scale border color
        this.chartInstance.priceScale('volume').applyOptions({
          borderColor: rgbaColor,
        })

        console.log('Volume color updated to:', rgbaColor)
      } */
    },
    convertHexToRGBA (hexColor, opacity) {
      // Convert hex color to RGB
      const red = parseInt(hexColor.substring(1, 3), 16)
      const green = parseInt(hexColor.substring(3, 5), 16)
      const blue = parseInt(hexColor.substring(5, 7), 16)

      // Return RGBA color string with the specified opacity
      return `rgba(${red}, ${green}, ${blue}, ${opacity})`
    },
    handleResize () {
      if (this.chartInstance) {
        const newWidth = Math.max(this.$refs.chartContainer.offsetWidth, this.minChartWidth)
        const newHeight = Math.max(this.$refs.chartContainer.offsetHeight, this.minChartHeight)
        // console.log('newWidth', newWidth, 'newHeight', newHeight)
        this.chartInstance.applyOptions({
          width: newWidth,
          height: newHeight,
        })
      }
    },
    scrollToRealTime  () {
      this.chartInstance.timeScale().scrollToRealTime()
    },

    scrollToBeginning  () {
      this.chartInstance.timeScale().scrollToPosition(-(this.candlestickData.length - 100), true)
    },
    transformData (dataArray) {
      return dataArray.map(row => ({
        time: row[0],
        open: row[1],
        high: row[3],
        low: row[4],
        close: row[2]
      }))
    },
    transformVolumeData (dataArray) {
      return dataArray.map(row => ({
        time: row[0],
        value: row[5] // Assuming the volume is in the sixth position
      }))
    },
    createChart_ () {
      const chartContainer = this.$refs.chartContainer
      if (this.chartInstance) {
        this.chartInstance.remove()
      }
      this.chartInstance = createChart(chartContainer,
        this.chartProperties
      )

      this.candlestickSeries = this.chartInstance.addCandlestickSeries()
      this.candlestickSeries.setData(this.candlestickData)

      const volumeColor = this.isNightMode ? 'rgba(122, 168, 228, 0.55)' : 'rgba(4, 111, 232, 0.25)'

      this.volumeSeries = this.chartInstance.addHistogramSeries({
        color: volumeColor,
        priceFormat: {
          type: 'volume',
        },
        priceScaleId: 'volume',
        scaleMargins: {
          top: 0.7,
          bottom: 0,
        },
      })
      this.volumeSeries.setData(this.volumeData)
      this.chartInstance.timeScale().subscribeVisibleTimeRangeChange(() => {
        const isAtRealTime = this.chartInstance.timeScale().scrollPosition() >= 0
        this.showEndButton = !isAtRealTime
      })

      // Subscribe to crosshair move event to update the tooltip data
      this.chartInstance.subscribeCrosshairMove((param) => {
        if (param.point === undefined || param.time === undefined) {
          if (this.lastValidTooltipData) {
            this.tooltipData = { ...this.lastValidTooltipData }
          }
          return
        }
        const bar = param.seriesPrices.get(this.candlestickSeries)
        const volume = param.seriesPrices.get(this.volumeSeries)

        if (bar) {
          this.tooltipData = {
            name: 'Symbol Name',
            date: new Date(param.time * 1000).toLocaleString('en', { timeZoneName: 'short' }),
            open: bar.open.toFixed(1),
            close: bar.close.toFixed(1),
            high: bar.high.toFixed(1),
            low: bar.low.toFixed(1),
            volume: volume ? volume.toFixed(1) : 0,
            color: bar.close > bar.open ? 'green' : 'red', // Example color logic
          }
        }
        this.lastValidTooltipData = { ...this.tooltipData }
      })
      this.volumeSeries.applyOptions({ visible: this.volumeSeriesVisible })

      const handleKeyPress = (event) => {
        const timeScale = this.chartInstance.timeScale()
        const currentPosition = timeScale.scrollPosition()
        const currentRange = timeScale.getVisibleLogicalRange()
        const rangeLength = currentRange.to - currentRange.from
        const zoomFactor = 0.2
        let newRangeLength

        switch (event.code) {
          case 'ArrowLeft':
            timeScale.scrollToPosition(currentPosition - 1000, false)
            break
          case 'ArrowRight':
            timeScale.scrollToPosition(currentPosition + 1000, false)
            break
          case 'Equal':
            newRangeLength = rangeLength * (1 - zoomFactor)
            break
          case 'Minus':
            newRangeLength = rangeLength * (1 + zoomFactor)
            break
          default:
            return
        }

        if (newRangeLength) {
          const newFrom = currentRange.from + (rangeLength - newRangeLength) / 2
          const newTo = newFrom + newRangeLength
          timeScale.setVisibleLogicalRange({ from: newFrom, to: newTo })
        }
      }

      document.addEventListener('keydown', handleKeyPress)

      // Store the handleKeyPress function so it can be removed later
      this.handleKeyPress = handleKeyPress

      this.chartInstance.timeScale().subscribeVisibleTimeRangeChange(() => {
        this.drawFibonacciRetracement()
      })
      console.log(this.volumeSeries)
    },
    toggleVolumeSeriesVisibility () {
      if (this.volumeSeries) {
        this.volumeSeriesVisible = !this.volumeSeriesVisible
        this.volumeSeries.applyOptions({ visible: this.volumeSeriesVisible })
      }
    },
    updateChartData () {
      if (!this.chartInstance) {
        // If the chart instance doesn't exist, create the chart
        this.createChart_()
      }
    },
    openChart () {
      this.showChart = true
      this.$nextTick(() => {
        this.handleResize()
      })
    },
    closeChart () {
      if (this.chartInstance) {
        this.chartInstance.remove()
        this.chartInstance = null
        window.removeEventListener('resize', this.handleResize)
        document.removeEventListener('keydown', this.handleKeyPress)
      }
      this.showChart = false
    },
    handleOutsideClickChart () {
      // this.closeChart()
    },
    openCharting (routes) {
      const id = this.getCurrentPathSegment().number
      this.openChart()
      if (this.$route.name === 'Backtest') {
        const mainStore = useMainStore()
        const startTime = performance.now() // Start time
        axios.post('/initial-charting', {
          id,
          config: mainStore.settings.backtest,
          routes: routes,
          start_date: this.backtestStore.tabs[id].form.start_date,
          finish_date: this.backtestStore.tabs[id].form.finish_date,
          destination: ['backtest', id],
          indicator_info: this.indicatorsInfo
        }).then(response => {
          const endTime = performance.now() // End time
          console.log('Initial charting took ' + ((endTime - startTime) / 1000) + ' seconds')
          this.candlestickData = this.transformData(response.data)
          this.volumeData = this.transformVolumeData(response.data)
          console.log(this.candlestickData.length)
          this.updateChartData()
        }).catch(error => {
          console.log(error)
        })
      } else if (this.$route.name === 'Optuna') {
        this.optunaStore.initialChartCreation(id, routes)
          .then(data => {
            this.candlestickData = data
            this.updateChartData()
          })
      } else if (this.$route.name === 'Optimization') {
        this.optimizationStore.initialChartCreation(id, routes)
          .then(data => {
            this.candlestickData = data
            this.updateChartData()
          })
      }
    },
    backendFormatCode () {
      axios.post('/code-formatting', {
        code: this.code
      }).then(response => {
        if (response.status === 200) {
          this.code = response.data
        } else {
          console.log(response.data)
        }
      }).catch(error => {
        console.error(error)
      })
    },
    showHelp () {
      this.showHelpModal = true
    },
    closeHelpModal () {
      this.showHelpModal = false
    },
    showHotkeys () {
      this.showHotkeysModal = true
    },
    closeHotkeysModal () {
      this.showHotkeysModal = false
    },
    setMonacoMarkers (editor, errorMessages) {
      const markers = this.parseErrors(errorMessages)
      monaco.editor.setModelMarkers(editor.getModel(), 'python', markers)
    },
    parseErrors (errorMessages) {
      const errors = Array.isArray(errorMessages) ? errorMessages : [errorMessages]
      return errors.map(error => {
        let startLineNumber = 1
        let startColumn = 1
        let endColumn = 2
        let message = error

        const lineColRegex = /<string>:(\d+):(\d+): (.*)/
        const lineColMatch = error.match(lineColRegex)
        const lineOnlyRegex = /line (\d+)/
        const lineOnlyMatch = error.match(lineOnlyRegex)

        if (lineColMatch) {
          startLineNumber = parseInt(lineColMatch[1])
          startColumn = parseInt(lineColMatch[2])
          message = lineColMatch[3]

          // Extract the quoted string from the message and adjust endColumn
          const quotedStringMatch = message.match(/'([^']+)'/)
          if (quotedStringMatch) {
            endColumn = startColumn + quotedStringMatch[1].length // Length of the quoted string
          } else {
            endColumn = startColumn + 1 // Default if no quoted string is found
          }
        } else if (lineOnlyMatch) {
          startLineNumber = parseInt(lineOnlyMatch[1])
          const lineContent = this.editorInstance.getModel().getLineContent(startLineNumber)
          endColumn = lineContent.length + 1
        }

        return {
          severity: monaco.MarkerSeverity.Error,
          startLineNumber: startLineNumber,
          startColumn: startColumn,
          endLineNumber: startLineNumber,
          endColumn: endColumn,
          message: message
        }
      })
    },

    setErrorMessages (error) {
      if (typeof error === 'string') {
      // Split the string into an array of lines if it contains newlines
        this.strategyError = error.includes('\n') ? error.split('\n') : [error]
      } else if (Array.isArray(error)) {
        this.strategyError = error
      } else {
        this.strategyError = ['Unknown error']
      }
    },
    editorWillMount (monaco) {

    },
    editorDidMount (editor) {
      this.setupHoverProvider(editor)
      this.setupAutocompleteProvider(editor)

      // Format document initially
      editor.getAction('editor.action.formatDocument').run()

      // Hotkey for formatting document
      editor.addCommand(monaco.KeyMod.Shift | monaco.KeyMod.Alt | monaco.KeyCode.KeyF, () => {
        editor.getAction('editor.action.formatDocument').run()
        this.backendFormatCode() // 'this' now correctly refers to the Vue component
      })

      // Add action for suggestions
      editor.addAction({
        id: 'show-suggestions',
        label: 'Show Suggestions',
        keybindings: [monaco.KeyMod.CtrlCmd | monaco.KeyCode.Space],
        run: function (ed) {
          ed.trigger('any-string', 'editor.action.triggerSuggest', {})
        }
      })

      // Event listener for content changes
