<template>
  <div v-if="isVisible" class="modal" :style="style">
    <div class="modal-content">
      <div class="modal-header">
        <span class="close" @click="closeModal">&times;</span>
        <div v-if="isObject(content)">
          <div v-for="(value, key) in content" :key="key">
            <span class="subtle-bold">{{ key }}:</span> {{ value }}
          </div>
        </div>
        <p v-else>{{ content }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HyperparametersModal',
  props: {
    isVisible: {
      type: Boolean,
      default: false
    },
    content: {
      type: [String, Object], // Accept both string and object
      default: ''
    }
  },
  methods: {
    closeModal () {
      this.$emit('close')
    },
    isObject (item) {
      return (item && typeof item === 'object' && !Array.isArray(item))
    }
  }
}
</script>

<style scoped>
.modal {
  position: absolute;
  z-index: 1000;
}

.modal-content {
    background-color: white;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
}

.modal-header {
  display: flex;
  align-items: center; /* Align items vertically */
}

.close {
  margin-right: 10px;
  cursor: pointer;
}

.subtle-bold {
  font-weight: 600
}
</style>
